<script>
    import { onMount } from 'svelte';
    import { jwtDecode } from 'jwt-decode';
    import { Link } from 'svelte-routing';
  
    $: photos = [];
    let userId = '';
    let isLoading = false; //  
    
    //   JWT     ID 
    function getIdFromToken() {
      const token = sessionStorage.getItem("token");
      if (!token) {
        console.error("No token found in session storage");
        return null;
      }
  
      try {
        const decoded = jwtDecode(token);
        console.log("Decoded token:", decoded);
        return decoded.id;
      } catch (error) {
        console.error("Error decoding token:", error);
        return null;
      }
    }
  
    //       
    onMount(async () => {
      userId = getIdFromToken();
      if (!userId) return;
  
      try {
        const response = await fetch(`/photo/load?id=${userId}`);
        if (response.ok) {
          photos = await response.json();
        } else {
          console.error("Failed to load photos:", await response.text());
        }
      } catch (error) {
        console.error("Error loading photos:", error);
      }
    });
  
    //   
    async function deletePhoto(photoId) {
      isLoading = true; //   

      try {
        const response = await fetch(`/photo/delete`, {
          method: 'POST', //  
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${sessionStorage.getItem('token')}`, //  
          },
          body: JSON.stringify({ id: photoId }), //   ID 
        });
  
        if (response.ok) {
          photos = photos.filter(photo => photo.img_id !== photoId); //    UI 
          console.log(`Photo ${photoId} deleted successfully`);
        } else {
          console.error(`Failed to delete photo ${photoId}:`, await response.text());
        }
      } catch (error) {
        console.error(`Error deleting photo ${photoId}:`, error);
      } finally {
        isLoading = false; //   
      }
    }
  </script>
  
  <style>
    .gallery {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
      gap: 20px;
      max-width: 1000px;
    }
  
    .photo {
      position: relative;
      width: 300px;
      height: 300px;
      border-radius: 12px;
      overflow: hidden;
      cursor: pointer;
      transition: transform 0.3s ease;
      box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
    }
  
    .photo img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.3s ease;
    }
   
    .photo-title {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: rgba(0, 0, 0, 0.6);
      color: white;
      text-align: center;
      padding: 10px;
    }

    .link {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 20px;
  font-size: 16px;
  font-weight: bold; /*   */
  text-align: center; /*    */
  display: inline-block; /*    */
  cursor: pointer;
  transition: background-color 0.3s ease, box-shadow 0.3s ease, transform 0.2s ease;
  text-decoration: none; /*     */
}

.link:hover {
  background-color: #0056b3;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transform: translateY(-2px); /*      */
}

.link:active {
  transform: translateY(2px); /*     */
  background-color: #004494; /*     */
}

.link:focus {
  outline: none; /*    */
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.5); /*    */
}

  .spinner {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
  border: 5px solid rgba(0, 0, 0, 0.1);
  border-top-color: #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  z-index: 1000;
  display: none; /*   */
}

.spinner.active {
  display: block;
}

@keyframes spin {
  0% {
    transform: translate(-50%, -50%) rotate(0deg); /*    */
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg); /*  360  */
  }
}


  </style>
  
<Link to="/photo?id={userId}"><div class="link">    </div></Link>


  <h2>    </h2>
  <div class="gallery">
    {#each photos as photo}
      <button 
        class="photo" 
        on:click={() => deletePhoto(photo.img_id)}>   
      
        <img src={photo.src} alt={photo.title} />
        <div class="photo-title">{photo.title}</div>
      </button>
    {/each}
  </div>

  <!--   -->
<div class="spinner {isLoading ? 'active' : ''}"></div>
